import React from 'react';
import ReactDOM from 'react-dom/client';

import App from './App';

import { AppProvider } from './context/AppContext';

const elem: HTMLElement | null = document.getElementById('root')

if (elem) {
  const root = ReactDOM.createRoot(elem);

  root.render(
    <React.StrictMode>
      <AppProvider>
        <App />
      </AppProvider>
    </React.StrictMode>
  )
}